import React, { useState } from 'react';
import { Modal, Button } from 'react-bootstrap';
import axios from 'axios'
import CardSuggestionsComponent from '../CardSuggestions/CardSuggestionsComponent';

export default function SearchCardComponent({ addCard, show, onHide }) {
    const [name, setName] = useState('')
    const [cardResponses, setCardResponses] = useState([])

    //suggestions modal
    const [showSuggestionsModal, setShowSuggestionsModal] = useState(false);
    const handleSuggestionsClose = () => setShowSuggestionsModal(false);
    const handleSuggestionsShow = () => setShowSuggestionsModal(true);

    const onNameChangeHandler = (event) => {
        setName(event.target.value)
    }
    const onSubmitHandler = (event) => {
        event.preventDefault()
        if (name.length > 0) {
            const id = Math.random();
            axios.all([
                axios.get(`https://db.ygoprodeck.com/api/v7/cardinfo.php?fname=${name}`),
            ])
            .then(response => {
                if (response[0] != null) {
                    var suggestions = response[0].data.data.slice(0, 5)
                    console.log (suggestions);
                    var newCardresponsesList = []
                    suggestions.map(s => {
                        const newCard = {
                            id: id,
                            name: s.name,
                            description: s.desc,
                            attribute: s.attribute,
                            level: s.level,
                            type: s.type,
                            atk: s.atk,
                            def: s.def,
                            imageUrl: s.card_images[0].image_url
                        }
                        newCardresponsesList = [...newCardresponsesList, newCard]
                    })
                    setCardResponses(newCardresponsesList)
                    setName('')
                }
                else {
                    console.log('error')
                }
                handleSuggestionsShow()
            })

        }
        onHide()
    }

    return (
        <div>
            <Modal show={show} onHide={onHide}>
                <Modal.Header>
                    <Modal.Title>Search Card</Modal.Title>
                    <Button variant="primary" onClick={onHide}>close</Button>
                </Modal.Header>
                <Modal.Body class="container-fluid text-center">
                    <form onSubmit={onSubmitHandler} className="form-group my-1">
                        <div>
                            <label className='mx-3'>Please enter the card name:</label>
                            <input type='text' value={name} onChange={onNameChangeHandler}></input>
                        </div>
                    </form>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="Success" className="btn btn-primary" onClick={onSubmitHandler}>Submit</Button>
                </Modal.Footer>
            </Modal>
            <CardSuggestionsComponent cardResponses={cardResponses} addCard={addCard} show={showSuggestionsModal} onHide={handleSuggestionsClose} />
        </div>
    )
}