import React from 'react'
import { Button } from 'react-bootstrap';

export default function CardSuggestionsItemComponent({card, addCard, onHide}) {
    const onSubmitHandler = (event) => {
        event.preventDefault()
        console.log(card);
        addCard({
            id: card.id,
            name: card.name,
            description: card.description,
            attribute: card.attribute,
            imageUrl: card.imageUrl,
            level: card.level,
            type: card.type,
            atk: card.atk,
            def: card.def
        })
        onHide()
    }
    return (
        <div className='border border-Secondary my-4'>
            <p>{card.name}</p>
            <img src={card.imageUrl} class="img-mid my-2 mx-4" alt="Responsive image"></img>
            <Button type="submit" className="btn btn-primary" onClick={onSubmitHandler}>Add to collection</Button>
        </div>
    )
}
