import React from 'react'
import { Button } from 'react-bootstrap';

export default function NavBarComponent({ onSelectThemeChange, onAddCardClick }) {
    return (
        <nav class="navbar navbar-expand-md navbar-dark bg-secondary px-2">
            <div class="container-fluid">
                <span class="navbar-brand mb-0 h1">Yu-Gi Tracker</span>
                <div class="d-flex">
                    <div class="d-flex">

                        <span class="navbar-brand mb-0 h1">Theme:</span>
                        <form>
                            <select class="form-select" aria-label="Theme selector" onChange={onSelectThemeChange}>
                                <option selected disabled>Default</option>
                                <option value={'light'}>Light</option>
                                <option value={'dark'}>Dark</option>
                            </select>
                        </form>
                    </div>
                    <Button class="btn btn-primary btn-sm mx-2" onClick={onAddCardClick}>Add Card</Button>
                </div>
            </div>
        </nav>
    )
}
