import React from 'react'
import { Modal, Button} from 'react-bootstrap';
import CardSuggestionsItemComponent from './CardSuggestionsItemComponent'

export default function CardSuggestionsComponent({cardResponses, addCard, show, onHide}) {
    return (
        <Modal show={show} onHide={onHide}>
        <Modal.Header>
            <Modal.Title>suggestions</Modal.Title>
            <Button variant="primary" onClick={onHide}>close</Button>
        </Modal.Header>
        <Modal.Body class="container-fluid text-center">
            {
            cardResponses.map(c => {
                return <CardSuggestionsItemComponent key={Math.random()} card = {c} addCard= {addCard} onHide = {onHide}/>
            })
            }
        </Modal.Body>
    </Modal>
    )
}
