import React from 'react'
import { Modal, Button } from 'react-bootstrap';
import '../../Custom.scss';

export default function CardDetailComponent({ card, show, onHide, onDelete }) {
    const removeHandler = () => {
        onDelete(card)
    }
    return (
        <Modal size="lg" show={show} onHide={onHide}>
            <Modal.Header>
                <Modal.Title>{card.name}</Modal.Title>
                <Button variant="primary" onClick={onHide}>close</Button>
            </Modal.Header>
            <Modal.Body class="container text-center py-2">
                <a href={card.imageUrl} target="_blank" rel="noopener noreferrer">
                    <img src={card.imageUrl} class="img-large" alt={card.name + ' card picture'} />
                </a>
                <div className='container mt-2'>
                    {card.type != 'Spell Card' && card.type != 'Trap Card' ? <div className='row mx-2 text-center justify-content-center'>
                        <div className='col-sm-6 border border-Secondary py-2'>
                            Attribute: {card.attribute}
                        </div>
                        <div className='col-sm-6 border border-Secondary py-2'>
                            Level: {card.level}
                        </div>
                    </div> : null}
                    <div className='row mx-2 text-center justify-content-center'>
                        <div className='col-md-12 border border-Secondary py-2'>
                            Card type: {card.type}
                        </div>
                    </div>
                    <div className='row mx-2 text-center justify-content-center'>
                        <div className='col-md-12 border border-Secondary py-2'>
                            Description: {card.description}
                        </div>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="danger" onClick={removeHandler}>
                    Delete
                </Button>
            </Modal.Footer>
        </Modal>
    )
}